.projects-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  margin-top: 25px;
  font-size: 25px;
  padding: 0 250px;

  h1 {
    border-bottom: 5px solid #ffb015;
    text-transform: uppercase;
  }

  
  .project {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-bottom: 25px;

    img {
      height: 50vh;
      width: 700px;
    }

    p {
      padding-left: 25px;
      width: 400px;
      font-size: 20px;
      line-height: 1.5;
      letter-spacing: 0.25px;
    }
  }
}